.profile__subtitle {
  margin: 0;
  padding-top: 16px;
  max-width: 100%;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #FFFFFF;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;

}

@media screen and (max-width: 992px) {
  .profile__subtitle {
    padding-top: 7px;
    font-size: 14px;
    line-height: 17px;
    max-width: 80%;
  }
}
